@import url(https://fonts.googleapis.com/css2?family=Merienda&family=Open+Sans:wght@300&display=swap);
* {
  font-family: Merienda, Verdana, Geneva, Tahoma, sans-serif;
}

body {
  margin: 0;
  font-family: Merienda , SourceSansPro-Regular , sans-serif;  
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: Merienda, source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.meals {
  align-items: center;
  background: #0fa36b;
  color: #464646;
  display: flex;
  flex-direction: column;
  font-family: sans-serif;
  height: 100vh;
  justify-content: center;
  width: 100%;
}

.rocksGlass {
  animation: shake 1s infinite ease;
  max-height: 80vh;
}

/*
